export function createScene(engine, canvas, BABYLON) {
  const scene = new BABYLON.Scene(engine);
  scene.clearColor = BABYLON.Color4.FromHexString("#16142BFF");

  const camera = new BABYLON.ArcRotateCamera("camera1", Math.PI / 2, Math.PI / 4, 10, BABYLON.Vector3.Zero(), scene);
  camera.attachControl(canvas, true);

  const light = new BABYLON.HemisphericLight("light1", new BABYLON.Vector3(1, 1, 0), scene);
  light.intensity = 0.7;

  const particleSystem = new BABYLON.ParticleSystem("particles", 100, scene);
  particleSystem.particleTexture = new BABYLON.Texture("https://i.imgur.com/5dNv2qM.gif", scene);
  particleSystem.emitter = new BABYLON.Vector3(0, 0, 0);

  particleSystem.minEmitBox = new BABYLON.Vector3(0, 0, 0);
  particleSystem.maxEmitBox = new BABYLON.Vector3(0, 0, 0);
  particleSystem.color1 = new BABYLON.Color4(0.98, 0.79, 0.11);
  particleSystem.color2 = new BABYLON.Color4(1, 0.48, 0.22);
  particleSystem.minSize = 0.1;
  particleSystem.maxSize = 0.5;
  particleSystem.minLifeTime = 0.2;
  particleSystem.maxLifeTime = 0.5;
  particleSystem.emitRate = 100;
  particleSystem.blendMode = BABYLON.ParticleSystem.BLENDMODE_ONEONE;
  particleSystem.gravity = new BABYLON.Vector3(0, -9.81, 0);
  particleSystem.direction1 = new BABYLON.Vector3(-1, -1, -1);
  particleSystem.direction2 = new BABYLON.Vector3(1, -1, 1);
  particleSystem.minAngularSpeed = 0;
  particleSystem.maxAngularSpeed = Math.PI;
  particleSystem.minEmitPower = 1;
  particleSystem.maxEmitPower = 3;
  particleSystem.updateSpeed = 0.005;

  let timeoutId;

  scene.onPointerMove = function (evt) {
    clearTimeout(timeoutId);
    const pickInfo = scene.pick(scene.pointerX, scene.pointerY);
    if (pickInfo.hit) {
      const position = pickInfo.pickedPoint;
      particleSystem.emitter = position;
    } else {
      const ray = scene.createPickingRay(scene.pointerX, scene.pointerY, BABYLON.Matrix.Identity(), camera);
      const ground = BABYLON.Vector3.Zero();
      const pickedPoint = ray.origin.add(ray.direction.scale(camera.radius));
      particleSystem.emitter = pickedPoint;
    }
    particleSystem.start();

    timeoutId = setTimeout(function () {
      particleSystem.stop();
    }, 200);
  };

  return scene;
}
